div.CanvasXpressCode {
    border: 1px solid rgb(83, 105, 255);
    border-radius: 5px;
    box-sizing: border-box;
    background-color: rgb(238, 238, 238);
    cursor: move;
    color: rgb(34, 34, 34);
    font: normal 12px arial, tahoma, sans-serif;
    margin: 2px 3px;
    padding: 10px 20px 20px 10px;
    position: absolute;
    white-space: nowrap;
}

img.CanvasXpressCode,
svg.CanvasXpressCode {
    background: rgba(0, 0, 0, 0) !important;
    border-radius: 5px;
    border: 1px solid rgb(83, 105, 255) !important;
    padding: 5px;
    box-shadow: 0 0 0px rgba(0, 0, 0, 0) !important;
    cursor: default;
    float: right;
    margin: 5px 8px 0px 0px !important;
    -moz-user-select: none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    -o-user-select: none;
    -ms-user-select: none;
}

pre.CanvasXpressCode {
    text-align: left;
    padding: inherit;
    margin: inherit;
    font-size: inherit;
    line-height: inherit;
    color: inherit;
    word-break: inherit;
    word-wrap: inherit;
    background-color: inherit;
    border: none;
    border-radius: inherit;
    counter-reset: line;
}

div.CanvasXpressCodeContainer {
    box-sizing: border-box;
    clear: both;
    width: 600px;
    overflow: auto;
    resize: horizontal;
    background-color: rgb(255, 255, 255);
    position: relative;
    top: 10px;
}

div.CanvasXpressCodeContent {
    box-sizing: border-box;
    margin: 2px 3px;
    padding: 2px 3px;
}

div.CanvasXpressCodeError {
    box-sizing: border-box;
    height: 36px;
    width: 300px;
    float: left;
    clear: none;
    resize: none;
    top: 5px;
    left: 20px;
    background-color: rgb(238, 238, 238);
    overflow: hidden;
    white-space: normal;
    color: rgb(255, 0, 0);
    position: relative;
    cursor: pointer;
}

pre.CanvasXpressCodeContent {
    margin: 0 !important;
    padding: 0 !important;
    white-space: pre;
    font-size: inherit;
    line-height: 0 !important;
    color: inherit;
    border: none !important;
    outline: none !important;
    cursor: text;
}

pre.CanvasXpressCodeContent:before {
    counter-reset: listing;
}

pre.CanvasXpressCodeContent code {
    counter-increment: listing;
    padding: 0 !important;
    white-space: inherit;
    line-height: 1.5;
}

pre.CanvasXpressCodeContent code::before {
    content: counter(listing) " ";
    display: inline-block;
    width: 3em;
    /* Fixed width */
    border-right: 1px solid rgb(221, 221, 221);
    padding-left: auto;
    margin-left: auto;
    text-align: right;
    /*color: rgb(136, 136, 136);*/
    background-color: rgb(239, 239, 239);
    -webkit-user-select: none;
}

/*
 * Carousel
 */

div.CanvasXpressCarouselScene {
    position: relative;
    margin: 0;
}

div.CanvasXpressCarousel {
    width: 100%;
    height: 100%;
    position: absolute;
    transform: translateZ(-288px);
    transform-style: preserve-3d;
    transition: transform 1s;
}

span.CanvasXpressCarousel {
    padding: 1px;
    position: absolute;
    border: 1px solid rgb(83, 105, 255);
    border-radius: 2px;
    line-height: 10px;
    cursor: pointer;
}

div.CanvasXpressCarouselCell {
    position: absolute;
    transition: transform 1s, opacity 1s;
    background-color: rgba(255, 255, 255, 0.95);
    border: 1px solid rgb(83, 105, 255);
    border-radius: 5px;
    backface-visibility: hidden;
}

div.CanvasXpressCarouselCell:nth-child(1) {
    transform: rotateY(0deg) translateZ(288px);
}

div.CanvasXpressCarouselCell:nth-child(2) {
    transform: rotateY(40deg) translateZ(288px);
}

div.CanvasXpressCarouselCell:nth-child(3) {
    transform: rotateY(80deg) translateZ(288px);
}

div.CanvasXpressCarouselCell:nth-child(4) {
    transform: rotateY(120deg) translateZ(288px);
}

div.CanvasXpressCarouselCell:nth-child(5) {
    transform: rotateY(160deg) translateZ(288px);
}

div.CanvasXpressCarouselCell:nth-child(6) {
    transform: rotateY(200deg) translateZ(288px);
}

div.CanvasXpressCarouselCell:nth-child(7) {
    transform: rotateY(240deg) translateZ(288px);
}

div.CanvasXpressCarouselCell:nth-child(8) {
    transform: rotateY(280deg) translateZ(288px);
}

div.CanvasXpressCarouselCell:nth-child(9) {
    transform: rotateY(320deg) translateZ(288px);
}

img.CanvasXpressCarousel:hover,
svg.CanvasXpressCarousel:hover {
    -webkit-transform: scale(1.8, 1.8);
    -ms-transform: scale(1.8, 1.8);
    transform: scale(1.8, 1.8);
    transition-duration: 0.3s;
    -webkit-transition-duration: 0.3s;
    /* Safari */
    box-shadow: 10px 10px 5px rgb(136, 136, 136);
    z-index: 2;
    position: relative;
    cursor: pointer;
    background: rgba(0, 0, 0, 0) !important;
    border: 0px none !important;
    box-shadow: 0 0 0px rgba(0, 0, 0, 0) !important;
    margin: 0px !important;
}


